@import '../ui.scss';

footer {
	padding: 2rem 10rem;
	background-color: $light;
}

.footer-link-ul {
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: center;
	list-style-type: none;
	padding: 0;
	margin: 0;
	font-size: 14px;
	width: 100%;
}

.footer-link {
	padding: 8px 0;
	margin-right: 2rem;
	
	.footer-link-navlink {
		text-decoration: none;
		color: $dark;
	}
}

#social-link {
	margin-left: auto;
	display: flex;
	align-items: center;

	.footer-link-navlink {
		text-decoration: none;
		color: $dark;
		margin-left: .5rem;
	}
}

#trademark {
	margin-top: 1rem;

	p {
		font-size: 13px;
	}
}

@media only screen and (max-width: 768px) {
	footer {
		padding: 2rem;
	}

	.footer-link-ul {
		flex-direction: column;
		justify-content: flex-start;
		align-items: flex-start;
	}
	
	.footer-link {
		padding: 0;
		margin: 0;
	}

	#social-link  {
		margin-left: 0;
		margin-top: 5px;
	}

	#trademark {
		p {
			width: 100%;
		}
	}
}