* {
    outline: none;
    box-sizing: border-box;
    letter-spacing: 1px;
    user-select: none;
    -webkit-user-drag: none;

    &:focus,
    &:hover {
        outline: none;
        -webkit-tap-highlight-color: transparent;
    }
}

a, button {
    transition: cursor 1s ease-in-out;
    // cursor: url('./assets/cursor_hand.svg'), auto;
}

html {
    font-family: "Inconsolata";
    scroll-behavior: smooth !important;
    // cursor: url('./assets/cursor_pointer.svg'), auto;
}


body {
    padding: 0;
    margin: 0;
    width: 100vw;
    height: 100vh;
    font-family: "Inconsolata";
    overflow: hidden;
    // cursor: url('./assets/cursor_pointer.svg'), auto;
}

*::-webkit-scrollbar {
    display: none;
}