@import "../ui.scss";

#overview-page-container {
    display: grid;
    grid-template-columns: auto auto;
	background-color: $white;
	padding: 5rem 10rem;
}

#overview-container-01 {
    display: flex;
    flex-direction: column;
}

#hello {
    position: relative;
    margin-left: 1.5rem;
	padding-left: .5rem;

    &::after {
        content: "";
        width: 1rem;
        height: 2px;
        background-color: $dark;
        display: block;
        position: absolute;
        left: -1.2rem;
        bottom: 0.45rem;
    }
}

#greet- {
    font-size: 60px;
    font-weight: 700;
}

#greet-subheading {
    font-size: 30px;
}

#greet-belowheading {
    margin-top: 2rem;
}

#overview-container-02 {
    overflow: hidden;
	padding-right: 4rem;
}

#location-map {
    transition: all 1s ease-out;
    cursor: pointer;

    #location-marker,
    circle {
        opacity: 0;
    }

    &:hover {
        transform: rotate(-20deg) scale(2) translateX(8rem) translateY(4rem);

        path {
            stroke: black;
			stroke-width: 1 !important;
        }

        #location-marker,
        circle {
            animation: showMark 1s ease-out forwards;
			animation-delay: .8s;
			stroke: red !important;
        }

        @keyframes showMark {
            from {
                opacity: 0;
                fill: transparent;
            } to {
                fill: transparent;
				opacity: 1;
                transform: rotate(20deg) translateX(2rem) translateY(-2.7rem);
			}
        }
    }
}

#getintouch {
    padding: 1rem;
    background-color: transparent;
    color: $dark;
    border: 1px solid $dark;
    font-size: 14px;
    position: static;
    width: 150px;
    text-decoration: none;
    transition: all 0.5s ease-out;
    margin-top: 1rem;
	white-space: nowrap;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;

    &:hover {
        background-color: $dark;
        color: $white;
    }
}



#portfolio-page-container {
	padding: 2rem 10rem;
}

.title-header {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
}

.title-header-span {
	font-weight: 800;
	font-size: 30px;
	position: relative;
	padding-left: 3rem;
	margin: 2rem 0;

	&::before {
		content: '';
		position: absolute;
		width: 2rem;
		height: 2px;
		background-color: $dark;
		display: block;
		left: 0;
		bottom: .9rem;
	}
}

.portfolio-grid {
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-gap: 1rem;
	transition: all 0.5s ease-out;
}

.portfolio-container-box {
	height: 100%;
	background-color: $white;
	overflow: hidden;
	border: 1px solid $white;
	transition: all 1s ease-out;
	position: relative;
	cursor: pointer;
	border: 1px solid $dark;
	
	img {
		transition: all 1.5s ease-out;
		width: 100%;
		height: 100%;
		transform: scale(1);
	}

	.overlay-details {
		width: 0;
		height: 100%;
		background-color: rgba(0, 0, 0, 0.5);
		position: absolute;
		right: -10rem;
		top: 0;
		padding: 2rem;
		transition: all 0.5s ease-out;
		display: grid;
		place-items: center;
		
		.overlay-details-heading {
			font-weight: 700;
			font-size: 50px;
			transition: all 0.5s ease-out;
			color: $white;
			white-space: nowrap;
		}
	}

	&:hover {
		img {
			transform: scale(1.5);
		}

		.overlay-details {
			left: 0;
			width: 100%;
		}
	}
}

.portfolio-base {
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
	text-decoration: none;
	color: $dark;

	.portfolio-header {
		font-weight: 600;
		font-size: 16px;
	}

	.portfolio-detail-text {
		font-size: 14px;
	}
}


#portfolio-about-me {
	padding: 0 10rem;

	p {
		text-indent: 10rem;
	}
}

#technologies {
	padding: 0 10rem;
}

.tech {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;

	i {
		display: grid;
		place-content: center;
		font-size: 100px;
		margin-bottom: 2rem;
		color: gray;
	}
}

@media only screen and (max-width: 768px) {
	#overview-page-container {
		grid-template-columns: 1fr;
		padding: 2rem;
	}

	#greet- {
		font-size: 40px;
	}

	#greet-subheading {
		font-size: 23px;
	}
	
	#greet-belowheading {
		width: 100%;
		white-space: wrap;
	}

	#overview-container-02 {
		overflow: hidden;
		width: 100%;
		padding-right: 0;
	}

	#location-map {
		width: 100%;
		overflow: hidden;

		&:hover {
			transform: rotate(-20deg) scale(2) translateX(3rem) translateY(4rem);
		}
	}

	#portfolio-page-container {
		padding: 2rem;
	}

	.portfolio-grid {
		grid-template-columns: 1fr;
	}

	.portfolio-container-box {
		width: 100%;
	}

	#portfolio-about-me{
		padding: 2rem;
		
		p {
			width: 100%;
			text-indent: 2rem;
			text-align: justify;
		}
	}

	#technologies {
		padding: 2rem;
	}

	.tech {
		grid-template-columns: 1fr 1fr;
		
	}
}