@import '../ui.scss';

#navbar {
	padding: 2rem 10rem;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	position: sticky;
	top: 0;
	background-color: $white;
	z-index: 9;
}

.logo {
	width: 2.65rem;
	height: 2.65rem;
}

#menubar-button {
	background: transparent;
	border: 0;
	position: relative;
	cursor: pointer;
	width: 3rem;
	height: 2rem;
	display: grid;
	place-items: center;

	span {
		display: block;
		width: 2rem;
		height: 2px;
		background-color: $dark;
		position: relative;

		&::before {
			content: '';
			position: absolute;
			background: $dark;
			width: 1rem;
			height: 2px;
			display: block;
			bottom: 5px;
		}

		&::after {
			content: '';
			position: absolute;
			background: $dark;
			width: 1rem;
			height: 2px;
			display: block;
			bottom: -5px;
			right: 0;
		}
	}
}


@media only screen and (max-width: 768px) {
	#navbar {
		padding: 2rem;
		position: relative;
	}
}